<template>
  <div class="container-fluid my-4">
    <div class="row justify-content-md-center mx-4 g-5">
      <div class="col">
        <div class="row">
          <h4>Encoded Base64</h4>
        </div>
        <div class="row justify-content-md-center">
          <textarea class="form-control" v-model="encoded_b64_textarea" rows="10"></textarea>
        </div>
      </div>
      <div class="col-md-auto align-self-center">
          <div class="row">
            <button type="button" class="btn btn-primary" @click="decode">Decode</button>
          </div>
          <br>
          <div class="row">
            <button type="button" class="btn btn-info" @click="encode">Encode</button>
          </div>
          <br>
          <div class="row">
            <button type="button" class="btn btn-light" @click="clear">Clear</button>
          </div>
      </div>
      <div class="col">
        <div class="row">
          <h4>Decoded Base64</h4>
        </div>
        <div class="row justify-content-md-center">
          <textarea class="form-control" v-model=decoded_b64_textarea rows="10"></textarea>
        </div>
      </div>
    </div>
  </div>
</template>

<style scoped>
#app {
  height: inherit;
}
h4 {
  text-align: center;
}
</style>

<script>
export default {
  data() {
    return {
      encoded_b64_textarea: '',
      decoded_b64_textarea: ''
    }
  }, 
  methods: {
    encode(event) {
      let encode_val = this.encoded_b64_textarea
      this.decoded_b64_textarea = btoa(encodeURIComponent(encode_val))
    },
    decode(event) {
      let decode_val = this.decoded_b64_textarea
      this.encoded_b64_textarea = atob(decodeURIComponent(decode_val))
    },
    clear() {
      this.decoded_b64_textarea = ''
      this.encoded_b64_textarea = ''
    }
  }
}
</script>
